@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');

*{
    box-sizing: border-box;
}

body{
    background-color: rgba(0, 0, 0, 0.1);
    margin: 0;
    padding: 0;
    height: auto;
    font-family: 'Roboto Mono', monospace;
}

html, 
body,
#root,
.container {
    height: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.container__bar{
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    z-index: 10;
    position: sticky;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: #000;
    color: rgba(255, 255, 255, 0.8);
    font-size: 20px;
}
.container__svg{
    margin-right: 10px;
    height: 60px;
    width: 60px;
}
.container__end{
    margin-left: 10px;
}


.container__bar h4 a{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.8);
    transition: color 0.2s ease-in-out;
}
.container__bar h4 a:hover{
    text-decoration: none;
    color: #f1f1f1;
    border-bottom: 3px solid #f1f1f1;
}